<template>
    <div class="container">
        <h1>Message Board</h1>
        <div class="main-wrap">
            <div class="board-wrap">
                <msg-text v-for="item in msgData"
                    :msgdata ="item"
                    @delete = "outDelete"
                    @edit = "outEdit"
                    :key="item.msg_id"
                />
            </div>
            <div class="control-wrap">
                <div class="input-wrap">
                    <label for="">暱稱</label>
                    <input type="text" v-model='recentlyMsg.name'>
                    <label for="">內容</label>
                    <input type="text" v-model='recentlyMsg.msg'>
                    <button @click="addMsg">發送</button>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
import axios from 'axios';
import msgText from './msgText';

export default {
    name: 'board',
    components: {
        msgText,
    },
    data() {
        return {
            recentlyMsg: {
                msg_id: '',
                name: '',
                msg: '',
            },
            msgData: [],
        };
    },
    created() {
        this.initPageMsgs();
    },
    methods: {
        initPageMsgs() {
            axios.get('/board').then((e) => {
                this.msgData = e.data.result;
            });
        },
        addMsg() {
            if (this.recentlyMsg.name === '' || this.recentlyMsg.msg === '') {
                return;
            }
            const sendmsg = {
                name: this.recentlyMsg.name,
                msg: this.recentlyMsg.msg,
            };
            if (this.recentlyMsg.msg_id !== '') {
                sendmsg.Msg_id = this.recentlyMsg.msg_id;
                console.log(sendmsg);
                axios.patch('/board', sendmsg).then((e) => {
                    if (e.data.status) {
                        this.recentlyMsg.msg_id = '';
                        this.recentlyMsg.name = '';
                        this.recentlyMsg.msg = '';
                        this.initPageMsgs();
                    }
                });
                return;
            }
            axios.post('/board', sendmsg).then((e) => {
                if (e.data.status) {
                    this.recentlyMsg.name = '';
                    this.recentlyMsg.msg = '';
                    this.initPageMsgs();
                }
            });
        },
        outDelete(id) {
            axios.delete('/board', { params: { msg_id: id } }).then((e) => {
                if (e.data.status) {
                    this.initPageMsgs();
                }
            });
        },
        outEdit(msg) {
            const msgs = { ...msg };
            this.recentlyMsg = msgs;
        },
    },
};
</script>


<style scoped>
    .container {
        margin: 0 auto;
        width: 500px;
        font-weight: 600;
    }

    .main-wrap {
        padding: 2%;
        border-radius: 10px;
        background: #4053BF;
    }

    .board-wrap {
        padding: 2%;
        background-color: #AEB2EE;
        height: 600px;
    }

    .control-wrap{
        margin-top: 10px;
        color: white;
        font-size: 1.2rem;
    }

    .input-wrap {
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    button {
        height: 35px;
        font-size: 1rem;
        border: none;
        background-color: midnightblue;
        border-radius: 5px;
        color: white;
        cursor: pointer;
    }

    input {
        height: 30px;
        font-size: 1.1rem;
        font-weight: 600;

    }
    .input-wrap>input:first-of-type {
        width: 60px;
    }

</style>



