<template>
  <div class="msg-wrap">
        <div class="msg-content">
        <span>{{msgdata.name}}</span>:
        <span>{{msgdata.msg}}</span>
        </div>
        <div class="btn-wrap">
            <button @click="editMsg(msgdata)">修改</button>
            <button @click="deleteMsg(msgdata.msg_id)">刪除</button>
        </div>
  </div>
</template>

<script>
export default {
    name: 'msgText',
    props: ['msgdata'],
    data() {
        return {

        };
    },
    methods: {
        deleteMsg(id) {
            this.$emit('delete', id);
        },
        editMsg(msg) {
            this.$emit('edit', msg);
        },
    },

};
</script>

<style scoped>
    .msg-wrap {
        padding: 0 5px;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 40px;
        background-color: #C9DBF8;
        border-radius: 5px;
    }

     .btn-wrap button {
        height: 30px;
        font-size: 1rem;
        color: white;
        border: none;
        border-radius: 2px;
        cursor: pointer;
     }

    .btn-wrap button:first-of-type {
        background-color: #7CC3CC;
    }

    .btn-wrap button:nth-of-type(2) {
        background-color: #DF3B40;
    }
</style>



